import React from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';

import Layout from "../components/layout";
import PlusIcon from '../styles/img/svg/br-add.svg';
import PlusIconSm from '../styles/img/svg/br-add-sm.svg';
import SEO from "../components/seo"

export default class Buttons extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
      disabled: false
    }
  }

  handleDisableAllClick = () => {
    this.setState({
      disabled: !this.state.disabled
    })
  }

  render(){
    return(
      <Layout>
        <SEO title="Buttons" />
        <div className="container">
        {/*--- SOLID ---*/}
        <section className="island px-4 py-3">
          <h2>
            <svg className="mr-2" width="30" height="30" viewBox="0 0 30 30" xmlns="http://www.w3.org/2000/svg">
              <rect x="0" y="0" width="30" height="30" rx="5" ry="5"/>
            </svg>
            Solid
            <button className="btn btn-link mt-2 mt-sm-0 ml-auto" onClick={this.handleDisableAllClick}>{this.state.disabled ? "Enable" : "Disable"} all</button>
          </h2>
          <div className="mt-3">
            <button className="btn btn-primary mr-3 mb-2" disabled={this.state.disabled}>Primary</button>
            <button className="btn btn-secondary mr-3 mb-2" disabled={this.state.disabled}>Secondary</button>
            <button className="btn btn-brand mr-3 mb-2" disabled={this.state.disabled}>Brand</button>
            <button className="btn btn-primary btn-icons mr-3 mb-2" disabled={this.state.disabled}>
              <PlusIcon />
            </button>
            <button className="btn btn-icons mr-3 mb-2" disabled={this.state.disabled}>
              <PlusIcon />
            </button>
            <button className="btn btn-link mb-2" disabled={this.state.disabled}>Button Link</button>
          </div>
          <div className="mt-3">
            <SyntaxHighlighter language="html" style={docco} className="rounded">
              {`<button class="btn btn-primary mr-3">Primary</button>\n<button class="btn btn-secondary mr-3">Secondary</button>\n<button class="btn btn-brand mr-3">Brand</button>\n<button class="btn btn-primary btn-icon mr-3"><svg>...</svg></button>\n<button class="btn btn-icon mr-3"><svg>...</svg></button>\n<button class="btn btn-link mb-2">Button Link</button>`}
            </SyntaxHighlighter>
          </div>
        </section>
        {/*--- OUTLINE ---*/}
        <section className="island px-4 py-3 mt-3">
          <h2>
            <svg className="mr-2" width="30" height="30" viewBox="0 0 34 34" xmlns="http://www.w3.org/2000/svg">
              <rect x="2" y="2" width="30" height="30" rx="5" ry="5" fill="none" stroke="#E21B23" strokeWidth="2" />
            </svg>
            Outline
          </h2>
          <div className="mt-3">
            <button className="btn btn-outline-primary mr-3 mb-2" disabled={this.state.disabled}>Primary</button>
            <button className="btn btn-outline-secondary mr-3 mb-2" disabled={this.state.disabled}>Secondary</button>
            <button className="btn btn-outline-brand mr-3 mb-2" disabled={this.state.disabled}>Brand</button>
            <button className="btn btn-outline-primary btn-icons mr-3 mb-2" disabled={this.state.disabled}>
              <PlusIcon />
            </button>
          </div>
          <div className="mt-3">
            <SyntaxHighlighter language="html" style={docco} className="rounded">
              {`<button class="btn btn-outline-primary mr-3">Primary</button>\n<button class="btn btn-outline-secondary mr-3">Secondary</button>\n<button class="btn btn-outline-brand mr-3">Brand</button>\n<button class="btn btn-outline-primary btn-icon mr-3"><svg>...</svg></button>`}
            </SyntaxHighlighter>
          </div>
        </section>
        {/*--- SMALL ---*/}
        <section className="island px-4 py-3 mt-3 mb-3">
          <h2>
            <svg className="mr-2" width="20" height="20" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg">
              <rect x="0" y="0" width="20" height="20" rx="5" ry="5"/>
            </svg>
            Small
          </h2>
          <div className="mt-3">
            <button className="btn btn-sm btn-primary mr-3 mb-2" disabled={this.state.disabled}>Primary</button>
            <button className="btn btn-sm btn-secondary mr-3 mb-2" disabled={this.state.disabled}>Secondary</button>
            <button className="btn btn-sm btn-brand mr-3 mb-2" disabled={this.state.disabled}>Brand</button>
            <button className="btn btn-sm btn-primary btn-icons mr-3 mb-2" disabled={this.state.disabled}>
              <PlusIconSm />
            </button>
            <button className="btn btn-sm btn-outline-primary btn-icons mr-3 mb-2" disabled={this.state.disabled}>
              <PlusIconSm />
            </button>
            <button className="btn btn-sm btn-icons mr-3 mb-2" disabled={this.state.disabled}>
              <PlusIconSm />
            </button>
          </div>
          <div className="mt-3">
            <SyntaxHighlighter language="html" style={docco} className="rounded">
              {`<button class="btn btn-sm btn-primary mr-3">Primary</button>\n<button class="btn btn-sm btn-secondary mr-3">Secondary</button>\n<button class="btn btn-sm btn-brand mr-3">Brand</button>\n<button class="btn btn-sm btn-primary btn-icon mr-3"><svg>...</svg></button>\n<button class="btn btn-sm btn--outline-primary btn-icon mr-3"><svg>...</svg></button>\n<button class="btn btn-sm btn-icon mr-3"><svg>...</svg></button>`}
            </SyntaxHighlighter>
          </div>
        </section>
      </div>
      </Layout>
    )
  }
}
